<template>
    <footer>
        {{footerData}}
    </footer>
</template>

<style lang="less" scoped>
    footer{
        position: fixed;
        left: 0;
        right: 0;
        bottom: 0;
        height: 96px;
        background: #825E68;
        font-size: 32px;
        color: #fff;
        text-align: center;
        line-height: 96px;
        background: #653a72;    
    }
</style>

<script>
export default {
  props: ['footerData'],
}
</script>





