<template>
	<footer>
		<van-tabbar v-model='actives' @change="cc()" >
		  <van-tabbar-item icon="shop">
		  	<span>黑卡</span>
		    <template slot="icon" slot-scope="props">
		      <img :src="actives==0 ? hicon.actives : hicon.normal" />
		    </template>
		  </van-tabbar-item>
		  <van-tabbar-item icon="chat">
		  	<span>专享好物</span>
		    <template slot="icon" slot-scope="props">
		      <img :src="actives==1 ? zicon.actives : zicon.normal" />
		    </template>
		  </van-tabbar-item>
		  <van-tabbar-item icon="contact">
		  	<span>我</span>
		    <template slot="icon" slot-scope="props">
		      <img :src="actives==2 ? wicon.actives : wicon.normal" />
		    </template>
		  </van-tabbar-item>
		</van-tabbar>
	</footer>
</template>
<script>
import "lib-flexible"
export default{
	data(){
		return{
			hicon:{
				normal: require('../assets/zxhw/card@2x.png'),
				actives: require('../assets/zxhw/card_1@2x.png')
			},
			zicon:{
				normal: require('../assets/zxhw/things_1@2x.png'),
				actives: require('../assets/zxhw/things@2x.png')
			},
			wicon:{
				normal: require('../assets/zxhw/default_1@2x.png'),
				actives: require('../assets/zxhw/default@2x.png')
			},
		    actives:0,
		}
	},
	props:{
		active:{
			type:Number,
			require:false,
			default:()=>{
				return 0
			}
		}
	},
	mounted(){
		this.actives = this.active;
	},
	methods:{
		cc(){
			let e = this.actives;
			switch(e){
				case 0: this.$router.push({'name':'index'});break;
				case 1: this.$router.push({'name':'zxhw'});break;
				case 2: this.$router.push({'name':'我'});break;
			}
		}
	}
}
</script>
<style lang="less">
footer{
	position: fixed;
	background-color: #fff;
	bottom:0;
	width:100%;
	height:100px;
	z-index: 2000;
	.van-tabbar-item{
		.van-tabbar-item__text{
			color:#bebebe;
		}
		&.van-tabbar-item--active{
			.van-tabbar-item__text{
				color:#6b0cad;
			}
		}
	}
}
</style>



