<template>
    <header>
        <i></i>
        <font class="returnGo">返回</font>
        {{title}}
    </header>
</template>

<style lang="less" scoped>
    header{
        position: fixed;
        z-index: 1000;
        left: 0;
        right: 0;
        top: 0;
        height: 90px;
        background: #825E68;
        font-size: 34px;
        color: #fff;
        text-align: center;
        line-height: 90px;
        background: url('../assets/imgs/add-bg.png')  no-repeat;
        background-size: 100% 100%;
        i{
            position: absolute;
            left: 18px;
            top: 0px;
            width: 19px;
            height: 90px;
            background: url('../assets/imgs/returnGo.png') no-repeat left center;
            background-size: 100%;
        }
        font.returnGo{
            position: absolute;
            left: 45px;
            top: 0px;
            line-height: 90px;
        }
        font.succ{
            position: absolute;
            right: 40px;
            top: 0px;
            line-height: 90px;
        }
    }
</style>

<script>
export default {
  props: ['title'],
  methods:{
      returnGo(){
          window.history.go(-1)
      }
  }
}
</script>



